import React from "react"


function NavBar(props){
    return(
    <div>
        <ul className="nav justify-content-end" style={{backgroundColor: "blue", paddingTop:'1%', padding:'1%', marginBottom:'2%'}}>
            <li className="nav-item" style={{color: "white", padding: '1% 1% 1% 1% '}}>
            <strong>Your account address is </strong> {props.data.accounts[0]}
            </li>
            <li className="nav-item" style={{color: "white", padding: '1% 1% 1% 1% '}}>
                <strong>Balance:</strong> {props.data.balance} <span className="iconify" data-icon="mdi-ethereum" data-inline="false"></span> <b>ETH</b>
            </li>
        </ul>
    </div>
    )
}

export default NavBar