import React, { Component } from "react";
import GamblingContract from "./contracts/Gambling.json";
import getWeb3 from "./getWeb3";
import Form from './Components/BettingForm.js'
import GamesContainer from './Components/GamesContainer'
import FundButton from './Components/FundButton.js'
import NavBar from './Components/NavBar.js'
import DeactivateButton from './Components/DeactivateButton.js'
import ActivateButton from './Components/ActivateButton.js'

import "./App.css";

class App extends Component {
  constructor() {
    super()
    this.state = {
      web3: null,
      accounts: null,
      contract: null,
      guess: false,
      amount: '',
      gamesHistory: [],
      balance: 0,
      contractBalance: 0,
      fundAmount: '',
      isOnline: false
    }
    this.handleChange = this.handleChange.bind(this)
    this.onSubmit = this.onSubmit.bind(this)
  }

  componentDidMount = async () => {
    try {
      // Get network provider and web3 instance.
      const web3 = await getWeb3();

      // Use web3 to get the user's accounts.
      const accounts = await web3.eth.getAccounts();

      // Get the contract instance.
      const networkId = await web3.eth.net.getId();
      const deployedNetwork = GamblingContract.networks[networkId];
      const instance = new web3.eth.Contract(
        GamblingContract.abi,
        deployedNetwork && deployedNetwork.address,
      );

      // Set web3, accounts, and contract to the state, and then proceed with an
      // example of interacting with the contract's methods.
      this.setState({ web3, accounts, contract: instance });
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert(
        `Failed to load web3, accounts, or contract. Check console for details.`,
      );
      console.error(error);
    }

    // get the owner of the contract in order to decide if funding-form will be rendered
    var contractOwner = await this.getOwner()
    console.log(contractOwner)
    this.setState({contractOwner})

    // get previous games of the user 
    const returnValues = await this.getGames()
    console.log("accounreturnValuests",returnValues )

    
    const gamesHistory = this.toObject(returnValues)
    this.setState({gamesHistory})

    // get my balance (for account[0])
    const b = await this.getMyDetails()
    const balance = this.state.web3.utils.fromWei(b, 'ether')
    console.log(balance)
    this.setState({balance})

    // get contract's balance
    const cb = await this.state.contract.methods.getBalance().call()
    const contractBalance = this.state.web3.utils.fromWei(cb, 'ether')
    this.setState({contractBalance})

    const isOnline = await this.state.contract.methods.online().call()
    this.setState({isOnline})

    console.log(this.state)
  };

  // get the independent arrays that returned from the contract and create json objects 
  toObject = (returnValues)=>{
    var gamesHistory = []
    for (let i = returnValues[0].length -1 ; i>=0; i--) {
      var singleGame = {
        gid: returnValues[0][i], 
        outcome: returnValues[1][i], 
        guess: returnValues[2][i], 
        bet: this.state.web3.utils.fromWei(returnValues[3][i], 'ether'), 
        wonTokens: this.state.web3.utils.fromWei(returnValues[4][i], 'ether'), 
        randomNumber: returnValues[5][i],
      };
      gamesHistory.push(singleGame)
    }
    return gamesHistory
  }
  

  // get the history of the user's games
  getGames = async () => {
    const { contract, accounts } = this.state;
    return new Promise(async function(resolve){
      var games = await contract.methods.getMyGames().call({from: accounts[0]})
      resolve(games)
    })
  }

  // get the my balance
  getMyDetails = async () => {
    const { web3, accounts } = this.state;
    
    return new Promise(function(resolve){
      var balance =  web3.eth.getBalance(accounts[0]);
      console.log(balance)
      resolve(balance)
    })
  }


  //this submit function refers to the action of betting
  async onSubmit(event) {
    event.preventDefault();
    const { web3, accounts, contract } = this.state;
    let guess = parseInt(this.state.guess)
    await contract.methods.winOrLose(guess).send({
      from: accounts[0],
      value: web3.utils.toWei(this.state.amount, "ether"),
      gas: 5000000
    })

    const returnValues = await this.getGames()
    const gamesHistory = this.toObject(returnValues)
    this.setState({gamesHistory})

    // get contract's balance
    const cb = await this.state.contract.methods.getBalance().call()
    const contractBalance = this.state.web3.utils.fromWei(cb, 'ether')
    this.setState({contractBalance})
  }

  //needed function to handle the forms
  handleChange(event) {
    const re = /[0-9]|\./;
    const { name, value, type, checked } = event.target
    if (type === "checkbox"){
      this.setState({
        [name]: checked
      })
    }else{
      if (event.target.value === '' || re.test(event.target.value)){
        this.setState({
          [name]: value
        })
      }
    }
  }

  //get the owner of the contract
  getOwner = async () =>{
    const {contract } = this.state;
    return new Promise(function(resolve){
      var ownerAddress = contract.methods.owner().call()
      resolve(ownerAddress)
    })
  }

  //this submit function refers to the action of funding
  fundOnSubmit = async (event) =>{
    event.preventDefault()
    const { web3, accounts, contract } = this.state;
    
    web3.eth.sendTransaction({
      from: accounts[0],
      to: contract._address,
      value: web3.utils.toWei(this.state.fundAmount, "ether") 
    })
    // get contract's balance
    const cb = await this.state.contract.methods.getBalance().call()
    const contractBalance = this.state.web3.utils.fromWei(cb, 'ether')
    this.setState({contractBalance})
  }

  deactivateOnSubmit = async (event) =>{
    event.preventDefault()
    const { accounts, contract } = this.state;
    await contract.methods.turnOffline().send({
      from: accounts[0]
    })
    window.location.reload(); 
  }

  activateOnSubmit = async (event) =>{
    event.preventDefault()
    const { accounts, contract } = this.state;
    await contract.methods.turnOnline().send({
      from: accounts[0]
    })
    window.location.reload(); 
  }
  


  // render the website
  render() {
    if (!this.state.web3) {
      return <div>Loading Web3, accounts, and contract...</div>;
    }
    return (
      <div className="App">
        <NavBar data={this.state}/>
        {this.state.isOnline ? <Form handleChange={this.handleChange} data={this.state} onSubmit={this.onSubmit} />: null }
        
        {/* conditional rendering. fund form appears only to the owner */}
    
        {this.state.contractOwner == this.state.accounts[0] ? <FundButton data={this.state} fundOnSubmit={this.fundOnSubmit} handleChange={this.handleChange}/> : null }
        <br/>
        {this.state.contractOwner == this.state.accounts[0] && this.state.isOnline ? <DeactivateButton deactivateOnSubmit={this.deactivateOnSubmit} /> : null }
        {this.state.contractOwner == this.state.accounts[0] && !this.state.isOnline ? <ActivateButton activateOnSubmit={this.activateOnSubmit} /> : null  }

        <br></br>
        <GamesContainer data={this.state} />

      </div>
    );
  }
}

export default App;
