import React from "react"

function Form(props) {
    return (
    <div> 
        <h1>Try your Luck!</h1>
        <br />
            <form onSubmit={props.onSubmit}>
            <input
                name="amount"
                value={props.data.amount}
                onChange={props.handleChange}
                placeholder="Amount"
                required
            />
            <br /><br />
            <label className="form-label">
                <input
                    type="radio"
                    name="guess"
                    value='1'
                    checked={props.data.guess === '1'}
                    onChange={props.handleChange}
                    required
                /> Higher
            </label>
            <br />
            <label>
                <input
                    type="radio"
                    name="guess"
                    value='0'
                    checked={props.data.guess === '0'}
                    onChange={props.handleChange}
                /> Lower
            </label>
            <br/><br/>
            <button className="btn btn-primary btn-lg" style={{width:'10%'}}>Bet</button>
            <br/>
        </form>
    <br/><br/>
    </div>
    )
}

export default Form