import React from "react";
import Game from './Game';


function GamesContainer(props) {
  var gamesHistory = props.data.gamesHistory

  const games = gamesHistory.map(game => 
  <Game 
  key = {game.gid}
  id = {game.gid}
  guess = {game.guess==true ? 'greater than 5' : 'less than 5'}
  outcome = {game.outcome==true ? 'Win! ðŸ˜ðŸ¥³ðŸ¤‘' : 'Lose ðŸ’©ðŸ¥¶ðŸ˜­'}
  bet = {game.bet}
  wonTokens = {game.outcome==true ? game.wonTokens : 0}
  randomNumber = {game.randomNumber}
  />)
  return (
    <div>
      <h1>Game History</h1>
      {games}
    </div>
  );
}

export default GamesContainer;
