import React from "react";

function FundButton(props) {
  return (
    <div>
        <h1>Fund Your Contract</h1>
        The balance of your contract is {props.data.contractBalance} <span className="iconify" data-icon="mdi-ethereum" data-inline="false"></span> <b>ETH</b>
        <br/><br/>
        <form onSubmit={props.fundOnSubmit}>
            <input
                name="fundAmount"
                value={props.data.fundAmount}
                onChange={props.handleChange}
                placeholder="Fund"
            />
            <br /><br />
            <button className="btn btn-primary btn-lg" style={{width:'10%'}}>Fund</button>
        </form>
    </div>
) 
}

export default FundButton;