import React from "react"

function Game(props){
    return (
        <div className="card">
          <div className="card-body">
          <div className="card-header">
            <h5>Game: {props.id}, {props.outcome}</h5>
          </div>
          <ul className="list-group list-group-flush">
            <li className="list-group-item">My guess was {props.guess}. The random number was {props.randomNumber} </li>
            <li className="list-group-item">I bet {props.bet} <span className="iconify" data-icon="mdi-ethereum" data-inline="false"></span><b>ETH</b></li>
            <li className="list-group-item">I won {props.wonTokens}<span className="iconify" data-icon="mdi-ethereum" data-inline="false"></span><b>ETH</b> </li>
            
          </ul>
          </div>
        </div>
      );
}

export default Game;